import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const credit = resolve => {
  import('components/enter/credit/credit').then(module => {
    resolve(module)
  })
}

const enter = resolve => {
  import('components/enter/enter').then(module => {
    resolve(module)
  })
}

const information = resolve => {
  import('components/information/information').then(module => {
    resolve(module)
  })
}

const basic = resolve => {
  import('components/information/basic/basic').then(module => {
    resolve(module)
  })
}

const phoneCheck = resolve => {
  import('components/information/phone-check/phone-check').then(module => {
    resolve(module)
  })
}

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/enter'
    },
    {
      path: '/enter',
      component: enter,
      children: [
        {
          path: '/',
          redirect: '/credit'
        },
        {
          path: '/credit',
          component: credit
        }
      ]
    },
    {
      path: '/information',
      component: information,
      children: [
        {
          path: '/basic',
          component: basic
        },
        {
          path: '/phone_check',
          component: phoneCheck
        }
      ]
    }
  ]
})



